<template>
  <div style="width:100%;">
    <div class="contents_title">
      <!-- <h3>{{ this.plantInfo.plnt_nm }} 키워드</h3> -->
      <h3>관련 키워드</h3>
    </div>
    <div class="contents-container">
      <div class="contents_item_full">
        <!-- <div v-for="(plantClass,id) in this.dataKeywordPlantListRowData" :key="id">
          <div v-if="plantClass.plnt_id == plantInfo.plnt_id">
            <button class="keyword" v-on:click="sendKeywordPlantList(plantClass)">
              {{ plantClass.plnt_keyword_vw }}
            </button> 
          </div>
        </div> -->
        <div v-for="(plantClass,id) in this.dataKeywordPlantListRowData" :key="id">
            <button class="button-keyword-item" v-on:click="sendKeywordPlantList(plantClass)" v-if="plantClass.plnt_id == plantInfo.plnt_id">
              {{ plantClass.plnt_keyword_vw }}
            </button> 
        </div>
      </div>
    </div>
  </div>
</template>
  
<script>
const dataKeywordPlantListRowData = [];

export default {
    props: ['plantInfo', 'dataKeywordPlantList' ],
    components: {
    },
    data() {
      return {
        dataKeywordPlantListRowData,
      };
    },
    created() {
      this.unfoldDataKeywordPlantList(this.dataKeywordPlantList);
    },
    mounted() {
    },
    updated() {
    },
    methods: {
      unfoldDataKeywordPlantList(dataKeywordPlantList) {
        var dataKeywordPlantListUnfold = [];

        for(var i = 0; i < dataKeywordPlantList.length; i++) {
          for(var j = 0; j < dataKeywordPlantList[i].plnt_list.length; j++) {
            dataKeywordPlantListUnfold.push({
                  cdtyp_cd: dataKeywordPlantList[i].cdtyp_cd, 
                  cd: dataKeywordPlantList[i].cd, 
                  plnt_keyword_vw: dataKeywordPlantList[i].plnt_keyword_vw, 
                  plnt_id: dataKeywordPlantList[i].plnt_list[j].plnt_id});  
          }
        }
        this.dataKeywordPlantListRowData = dataKeywordPlantListUnfold;
      },
      sendKeywordPlantList(plantClass) {
          // 선택한 키워드 관련 작물 id를 찾아서 상위 컴포넌트로 보낸다.
          var plantList = [];
          var i = 0;

          for (i; i < this.dataKeywordPlantListRowData.length; i++) {
              if(this.dataKeywordPlantListRowData[i].cdtyp_cd == plantClass.cdtyp_cd && this.dataKeywordPlantListRowData[i].cd == plantClass.cd) {
                  plantList.push({plnt_id: this.dataKeywordPlantListRowData[i].plnt_id});
              }
          }
          this.$emit('sendKeywordPlantList', plantClass.plnt_keyword_vw, plantList);  // plantId
        }
    },
  };
  </script>