<template>
  <div class="contents_title">
    <!-- <h3>{{ this.plantInfo.plnt_nm }} 기본 정보</h3> -->
    <h3>기본 정보</h3>
  </div>
  <div class="contents-container" v-if="this.showPlantInfo == false">
    식물을 선택해보세요.
  </div>
  <div class="contents-container" v-else>
    <div class="contents_item">
      <div v-if="this.plantImgUrl != ''" style="text-align: center;">
        <img class="image_card" :src="this.plantImgUrl">
      </div>
      <div class="image_card" v-else>
        No Images.
      </div>
    </div>
    <div class="contents_item">
      <div style="padding-left:1rem; text-align: left;">
        <ul>
          <li>이름 : {{ plantInfo.plnt_nm }}</li>
          <li>원산지 : {{ plantInfo.orgn_nm }}</li>
          <li>학명 : {{ plantInfo.scntf_nm }}</li>
          <li>과 : {{ plantInfo.fmly_kor_nm }}</li>
          <li>속 : {{ plantInfo.gns_kor_nm }}</li>
          <li>종 : {{ plantInfo.spcs_kor_nm }}</li>
          <li>발아적절온도 : {{ plantInfo.grmntn_min_tmprtr_val }} ~ {{ plantInfo.grmntn_max_tmprtr_val }}</li>
          <li>생육적절온도 : {{ plantInfo.grwng_min_tmprtr_val }} ~ {{ plantInfo.grwng_max_tmprtr_val }}</li>
        </ul> 
      </div>
      <div>
        <BarChart :chartData="chartData"
                  :chartOptions="chartOptions"
                  :width="100"
                  :height="50">
        </BarChart>
      </div>
    </div>
  </div>

</template>

<script>
import BarChart from './../chart/BarChart.vue';

export default {
  props: {
    plantInfo: Object,
  },
  components: {
    BarChart,
  },
  data() {
    return {
      plantImgUrl: '',
      showPlantInfo: false,
      chartData: {
        labels : ['최저 온도', '최고 온도'],
        datasets : [{
              label: '온도 (°C)',
              // data: [this.lowestTemperature, this.highestTemperature],
              data: [0, 0],
              backgroundColor: [
                'rgba(255, 99, 132, 0.5)', // 최고 온도 막대의 배경색
                'rgba(54, 162, 235, 0.5)' // 최저 온도 막대의 배경색
              ],
              borderColor: [
                'rgba(255, 99, 132, 1)',
                'rgba(54, 162, 235, 1)'
              ],
              borderWidth: 1
          }],
        },
      chartOptions : {
        scales : {
                y: {
                  beginAtZero: true
                }
              },
        responsive : true
      }
    };
  },
  created() {
  },
  mounted() {
  },
  updated() {
    // 이미지 표시 정제
    this.checkPlantImage();

    // 온도 차트 값 update
    this.chartData.datasets[0].data = [this.plantInfo.grwng_min_tmprtr_val, this.plantInfo.grwng_max_tmprtr_val];
      // 더 상세히 변경하고 싶을 때.
        // chartData: {
        //     labels : ['최저 온도', '최고 온도'],
        //     datasets : [{
        //           label: '온도 (°C)',
        //           // data: [this.lowestTemperature, this.highestTemperature],
        //           data: [20, 20],
        //           backgroundColor: [
        //             'rgba(255, 99, 132, 0.5)', // 최고 온도 막대의 배경색
        //             'rgba(54, 162, 235, 0.5)' // 최저 온도 막대의 배경색
        //           ],
        //           borderColor: [
        //             'rgba(255, 99, 132, 1)',
        //             'rgba(54, 162, 235, 1)'
        //           ],
        //           borderWidth: 1
        //       }],
        //     },
        //   chartOptions : {
        //     scales : {
        //             y: {
        //               beginAtZero: true
        //             }
        //           },
        //     responsive : true
        //   }

  },
  methods: {
    checkPlantImage() {
      console.log('this.checkPlantImage();');

      if (typeof this.plantInfo.plnt_nm === 'undefined') {
        this.showPlantInfo = false;
      } else {
        this.showPlantInfo = true;

        // console.log('this.plantInfo ::: ', this.plantInfo);

        if (this.plantInfo.file_url != 'null') {
          // console.log('this.plantInfo.file_url is not null!!')
          try {
            this.plantImgUrl = require(`@/assets/images/${this.plantInfo.file_url}`);
            console.log('Get Image Url Success.')
          } catch(e) {
            console.log(e.message);
            console.log("Image File ", this.plantInfo.file_url, 'is not exist.');
            this.plantImgUrl = '';
          }
          // console.log("this.plantImgUrl ::: ", this.plantImgUrl);
        } else {
          console.log('Props data plantInfo.file_url is null.')
          this.plantImgUrl = '';
        }
      }
    }
  },
};
</script>