<template>
  <div class="contents-container" style="border:1px solid lightgray;">
    <div class="keyword-list" >
      <button class="button-basic" v-on:click="$emit('setAllPlantList');">전체보기</button>
      <button class="button-keyword-item" 
              v-for="(keywordPlant,id) in this.dataKeywordPlantList" :key="id" 
              v-on:click="sendKeywordPlantList(keywordPlant)">
        {{ keywordPlant.plnt_keyword_vw }}
      </button>
    </div>
    <!-- <div style="border-bottom:1px solid lightgray; width:100%; height:2rem; "> -->
    <!-- <div style="border:1px solid lightgray; width:100%; height:2rem; ">
      <div v-if="showKeywordPlantListYn">
        선택한 키워드 : {{ plantKeywordClicked }}
        <button class="button-basic" v-on:click="$emit('setAllPlantList');">전체보기</button>
      </div>
      <div v-else>
        전체목록
      </div>
    </div> -->
    <div class="plant-list" >
      <div  class="plant-list-item" 
            v-for="(plant,id) in plantList" :key="id" 
            v-on:click="$emit('plantClicked', plant)">
        {{ plant.plnt_nm }}
      </div>
    </div>
  </div>
</template>

<script>
const plantKwdList = [];

export default {
  props: ['plantList', 'dataKeywordPlantList', 'showKeywordPlantListYn', 'plantKeywordClicked'], 
  components: {
  },
  data() {
    return {
      plantKwdList,
    };
  },
  created() {
    var plantKwdListTemp = [];
    for (var i=0; i<this.dataKeywordPlantList.length; i++) {
      // plantKwdListTemp.push(dataPlantKeywordList[i].plnt_keyword_vw);
      plantKwdListTemp.push(this.dataKeywordPlantList[i].plnt_keyword_vw);
    }
    this.plantKwdList = plantKwdListTemp.filter((value, index, self) => self.indexOf(value) === index);
  },
  mounted() {
  },
  updated() {
  },
  methods: {
    sendKeywordPlantList(keywordPlant) {
      var paramPlantKeyword = [];
      for (var i = 0; i < keywordPlant.plnt_list.length; i++) {          
          paramPlantKeyword.push({
                                  cdtyp_cd:keywordPlant.cdtyp_cd,
                                  cd:keywordPlant.cd,
                                  plnt_keyword_vw:keywordPlant.plnt_keyword_vw,
                                  plnt_id:keywordPlant.plnt_list[i].plnt_id,
                                  plnt_nm:keywordPlant.plnt_list[i].plnt_nm,
                              });
      }
      this.$emit('sendKeywordPlantList', keywordPlant.plnt_keyword_vw, paramPlantKeyword);  // 
    }
  },
};
</script>